.popup__container {
    width: 430px;
    background-color: #FFFFFF;
    border-radius: 10px;
    box-sizing: border-box;
    position: relative;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, .15);
    padding: 34px 36px 37px;
}

@media screen and (max-width: 660px) {
    .popup__container {
        width: 282px;
        padding: 25px 22px;
}    
}