.footer {
    max-width: 880px;
    margin: 0 auto;
    padding: 67px 0 60px;
}

@media screen and (max-width: 930px) {
    .footer {
        max-width: 650px;
    }    
}

@media screen and (max-width: 660px) {
    .footer {
        max-width: 284px;
        padding: 48px 0 36px;
    }    
}